@import url(https://fonts.googleapis.com/css2?family=PT+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#colorsearch{
    padding: 16px;
    background-color: #999999;
}


#palette{
    display: flex;
    flex-direction: row;
    height: 35px;
    margin-bottom: 20px;
}

#add-color-button{
    background-color: #dddddd;
    border: 5px solid #333333;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    width: 12.5%;
}

#swatch {
    box-shadow: 0 0 0 1px rgba(0,0,0,.1);
    cursor: pointer;
    width: 12.5%;
}

#color {
    border-radius: 2px;
}
#popover {
    position: absolute;
    z-index: 2;
}

#cover{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}


html, body, :root {
    height: 100%;
}

:root{
  background-color: #3A3A3C;
}

#search-button{
  background-color: #dddddd;
  border: 5px solid #333333;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  width: 12.5%;
  height: 35px;
}

img {
  image-rendering: optimizeSpeed;
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: optimize-contrast;
  image-rendering: pixelated;
  -ms-interpolation-mode: nearest-neighbor;
}


.flex-container{
  color: whitesmoke;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.search-container{
  display: inline-block;
  margin: 0 1em;
}

.search-element{
  text-align: right;
  margin: 0.3em;
}

.top-bar{
  padding: 16px;
  background-color: #999999;
}

.pixel-result{
  margin: 1em;
}

.artwork{
  text-align: center;
}
.zoom-buttons{
  text-align: center;
}
a {
  color: rgb(59, 59, 59);
}

a:hover {
  color: black;
}

.color-wrapper{
  display: flex;
  flex-wrap: wrap;
}

.color-tile{
  font-family: 'PT Mono', monospace;
  font-size: 12px;
  text-transform: uppercase;
  
  margin: 5px 10px 5px 0;
  padding: 5px 8px;
  line-height: 22px;
  vertical-align: middle;
}

.hover-visible{
  background: inherit;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-filter: invert(1) grayscale(1) contrast(8);
          filter: invert(1) grayscale(1) contrast(8);
  opacity: 0;
}

.icon{
  background: inherit;
  color: white;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-filter: invert(1) grayscale(1) contrast(8);
          filter: invert(1) grayscale(1) contrast(8);
}

.hover-visible:hover{
  opacity: 1;
}

.detail-view{
  background-color: rgb(139, 139, 139);
}

.detail-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.image-large{
  flex-basis: 50%;
  flex-grow: 1;
  text-align: center;
  align-self: center;
}

.image-metadata{
  flex-basis: 50%;
  flex-grow: 1;
  max-width: 70%;
}
.select-all{
  float: right;
  text-align: right;
}
